/* eslint-disable */
import * as THREE from "three";
import { FBXLoader } from "three/examples/jsm/loaders/FBXLoader.js";
import {
    ArToolkitSource,
    ArToolkitContext,
    ArMarkerControls,
} from "@ar-js-org/ar.js/three.js/build/ar-threex";

import { onMounted } from "vue";

export default function () {
    var scene, camera, renderer;
    var arToolkitSource, arToolkitContext;
    var markerRoot;
    var mesh;

    function initialize() {
        scene = new THREE.Scene();

        let ambientLight = new THREE.AmbientLight(0xcccccc, 0.5);
        scene.add(ambientLight);

        camera = new THREE.Camera();
        scene.add(camera);

        renderer = new THREE.WebGLRenderer({
            antialias: true,
            alpha: true,
        });
        renderer.setClearColor(new THREE.Color("lightgrey"), 0);
        renderer.setSize(640, 480);
        renderer.domElement.style.position = "absolute";
        renderer.domElement.style.top = "0px";
        renderer.domElement.style.left = "0px";
        renderer.domElement.classList = ["rendererDiv"];
        document
            .querySelector("#ArSceneDom")
            .appendChild(renderer.domElement);

        /**初始化AR */
        arToolkitSource = new ArToolkitSource({
            sourceType: "webcam",
        });

        function onResize() {
            arToolkitSource.onResizeElement();
            arToolkitSource.copyElementSizeTo(renderer.domElement);
            if (arToolkitContext.arController !== null) {
                arToolkitSource.copyElementSizeTo(
                    arToolkitContext.arController.canvas
                );
            }
        }

        arToolkitSource.init(function onReady() {
            onResize();
        });

        // handle resize event
        window.addEventListener("resize", function () {
            onResize();
        });

        /**设置ARToolKit上下文 */
        arToolkitContext = new ArToolkitContext({
            cameraParametersUrl: "./arMarkers/camera_para.dat", // 记得引入
            detectionMode: "mono",
        });

        // copy projection matrix to camera when initialization complete
        arToolkitContext.init(function onCompleted() {
            camera.projectionMatrix.copy(
                arToolkitContext.getProjectionMatrix()
            );
        });

        /**设置定位点 */
        markerRoot = new THREE.Group();
        scene.add(markerRoot);
        new ArMarkerControls(arToolkitContext, markerRoot, {
            type: "pattern",
            patternUrl: "./arMarkers/hiro.patt",
        });

        initModel("./arModels/mainp.fbx", 0.002, () => {
            markerRoot.add(mesh);
            onResize();
        });
    }


    /**加载模型 */
    function initModel(modelPath, modelScale = 0.002, afterFunc) {
        var loader = new FBXLoader();
        loader.load(modelPath, (loadedMesh) => {
            loadedMesh.scale.set(modelScale, modelScale, modelScale);
            mesh = loadedMesh;
            scene.add(mesh);
            afterFunc();
        });
    }

    /**每帧更新AR */
    function update() {
        if (arToolkitSource.ready !== false)
            arToolkitContext.update(arToolkitSource.domElement);
    }

    /**threejs渲染函数 */
    function render() {
        renderer.render(scene, camera);
    }

    /**threejs逐帧 */
    function animate() {
        requestAnimationFrame(animate);
        update();
        render();
    }

    onMounted(() => {
        initialize();
        animate();
    });
}